import React from 'react';
import {
    BrowserRouter,
    Routes,
    Route,
} from "react-router-dom";
import MainNav from './components/mainNav'


class App extends React.Component{
    render(){
        return <BrowserRouter>
        <Routes>
                    <Route exact path='/' element={<MainNav/>} />
        </Routes>
        </BrowserRouter>
    }
}

export default App